import React, { Component } from 'react';
import "../../css/mypage/mainmypage.css";
class MainMypageComponent extends Component{

    constructor (props){
        super(props);

        this.state= {
            userCode:"",
            userName:"",
            userAge:"",
            userId:"",
            userEmail:"",
            userGender:"",
            userHostCafe:""
        }
    }

    componentDidMount(){
        this.loadUserInfo();
    }

    loadUserInfo = () => {
        fetch(`http://localhost:8080/cafe_user/1`) // login 이후 '1'을 param 변경.
            .then((response) => response.json())
            .then(response =>{
                let user = response;
                this.setState({
                    userCode: user.user_code,
                    userName: user.user_name,
                    userAge: user.user_age,
                    userId: user.user_id,
                    userEmail: user.user_email,
                    userGender: user.user_gender,
                    userHostCafe: "#"
                })
            })

            .catch(err => {
                console.log('loadUserInfo() Error!',err);
            })
    }

    render()
    {
        return (
            <main className="main-info">
                <form className="info-container">
                    <div className="about-user">
                        <div className="user-avatar-info">
                            <div className="input-container">
                                <h4 className="info-text">Name</h4>
                                <input disabled className="info-input" type="text" value={this.state.userName}/>
                            </div>
                            <div className="input-container">
                                <h4 className="info-text">Age</h4>
                                <input disabled className="info-input" type="Number" value={this.state.userAge}/>
                            </div>
                            <div className="input-container">
                                <h4 className="info-text">Gender</h4>
                                <h3 className="h3-font">{this.state.userGender}</h3>
                            </div>
                        </div>
                        <div className="user-sub-info">
                            <div className="input-container">
                                <h4 className="info-text">User Id</h4>
                                <input disabled className="info-input" type="text" value={this.state.userId}/>
                            </div>
                            <div className="input-container">
                                <h4 className="info-text">Email</h4>
                                <input disabled className="info-input" type="email" value={this.state.userEmail}/>
                            </div>
                            <div className="input-container">
                                <h4 className="info-text">Host Cafe</h4>
                                <a className="a-font" href={this.state.userHostCafe}>Please register your cafe</a>
                            </div>
                        </div>
                    </div>
                    <div className="info-child-container">
                        <button className="edit-btn">
                            <h1 className="info-font">Edit Profile</h1>
                        </button>
                        <a href="#">
                            <div className="cafe-btn">
                                <h1 className="info-font yellow-font"><a href={"http://localhost:3000/cafe-register"}>카페 등록하기</a></h1>
                            </div>
                        </a>
                    </div>
                </form>

                <div className="info-container">
                    <div className="about-cafe">
                        <div className="prefer-cafe-container">
                            <h4 className="info-text">My favorite cafe</h4>
                            <div className="favorite-cafe-link">
                                <a href="#">Go to favorite cafe</a>
                            </div>
                        </div>
                        <div className="bar"></div>
                        <div className="my-review-container">
                            <h4 className="info-text">My reviews</h4>
                            <ul>
                                <li className="review-list">
                                    <a href="#">아주 맛있어요</a>
                                </li>
                                <li className="review-list">
                                    <a href="#">개같이 멸망</a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </main>

        );
    }
}


export default MainMypageComponent;